@import url("https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

h2 {
  font-size: 20px;
  color: #0d2f4f;
}

.first-sec {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0062be;
  transition: 0.5s;
}

.item_user a {
  text-decoration: none;
}

.thumb li {
  list-style: none;
  display: inline-block;
  margin: 0 20px;
  cursor: pointer;
  transition: 0.5s;
}

.sci img {
  width: 30px;
}

.thumb li img {
  width: 70px;
  height: 70px;
}

.sci li {
  list-style: none;
}

.sci li a {
  display: inline-block;
  filter: invert(1);
  margin: 5px 0;
  transform: scale(0.9);
}

.nav-bar {
  position: fixed;
  background: #0062be;
  z-index: 1;
  top: 0;
  left: 0;
  height: 50px;
  z-index: 999;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-items ul{
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-bar .logo {
  position: relative;
  max-width: 80px;
}

.nav-bar .logo img {
  width: 70px;
  height: 40px;
}

.my-account {
  color: #fff;
  text-transform: uppercase;
  display: flex;
  width: 300px;
  min-width: 280px;
  align-items: center;
  justify-content: space-around;
}

.two {
  display: flex;
}

.my-account .item {
  font-size: 20px;
  margin-left: 35px;
}

.item-search {
  font-size: 20px;
}

.cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-radius: 3px;
  border-bottom: 2px solid #0062be;
  margin: 10px 10px;
}

.my-account .cart-item {
  background: red;
  font-size: 12px;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  position: relative;
}

.cart-item::before {
  content: "";
  width: 15px;
  height: 2.5px;
  background-color: red;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.menu-bar {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  gap: 10px;
  width: 40px;
  height: 50px;
}

.menu-bar span {
  width: 32px;
  height: 2.7px;
  border-radius: 2px;
  background-color: #fff;
  display: inline-block;
  margin-left: auto;
}

.nav-bar ul li {
  list-style: none;
  padding-left: 20px;
}

.nav-bar ul li a {
  display: inline-block;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  text-decoration: none;
}

.content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content .text-box {
  position: relative;
  max-width: 600px;
  min-width: 300px;
}

.content .text-box h2 {
  color: #fff;
  font-size: 5vw;
  line-height: 1.5em;
  font-weight: 900;
  text-transform: uppercase;
}

.content .text-box h2 span {
  font-size: 1.7em;
  color: #93b4d3;
}

.login-title p {
  font-style: italic;
  text-decoration: underline;
}

.content .text-box p {
  color: #fff;
}

.content .text-box a {
  display: inline-block;
  margin-top: 20px;
  padding: 8px 20px;
  background: #fff;
  color: #111;
  border-radius: 40px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

.nav-bar ul li a:hover {
  color: #0d2f4f;
  transition: 200ms;
}

.content .img-box {
  width: 600px;
  display: flex;
  padding-right: 50px;
  margin-top: 50px;
  justify-content: flex-end;
}

.content .img-box img {
  min-width: 300px;
  width: 30vw;
}

.thumb {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  display: flex;
}

.thumb li:hover {
  transform: translateY(-15px);
}

.sci {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #93b4d3;
  clip-path: circle(600px at right 800px);
}

/* form */

.section-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-title {
  font-size: 20px;
  padding-bottom: 30px;
  color: #fff;
  display: flex;
  justify-content: space-between;
}

.form {
  position: relative;
  width: 20rem;
}

.form__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  border-bottom: 2.5px solid #fff;
  font-family: inherit;
  font-size: 19px;
  color: #fff;
  outline: none;
  padding: 1.25rem;
  background: none;
}

.form__input:hover {
  border-color: rgb(67, 130, 133);
}

.form__input:focus {
  border-color: rgb(8, 174, 239);
}

.form__label {
  position: absolute;
  left: 1rem;
  top: 0.8rem;
  padding: 0 0.5rem;
  color: #fff;
  cursor: text;
  transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
}

.form__input:focus ~ .form__label,
.form__input:not(:placeholder-shown).form__input:not(:focus) ~ .form__label {
  top: -0.4rem;
  font-size: 0.9rem;
  left: 0.8rem;
}

.form__input:not(:-moz-placeholder-shown).form__input:not(:focus) ~ .form__label {
  top: -0.4rem;
  font-size: 0.9rem;
  left: 0.8rem;
}

.form__input:not(:-ms-input-placeholder).form__input:not(:focus) ~ .form__label {
  top: -0.4rem;
  font-size: 0.9rem;
  left: 0.8rem;
}

.form2 {
  position: relative;
  width: 20rem;
  height: 3rem;
  margin-top: 20px;
}

.form2__input2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  border-bottom: 2.5px solid #fff;
  font-family: inherit;
  font-size: 19px;
  color: #fff;
  outline: none;
  padding: 1.25rem;
  background: none;
}

::-webkit-scrollbar {
  display: none;
}

.form2__input2:hover {
  border-color: rgb(67, 130, 133);
}

.form2__input2:focus {
  border-color: rgb(8, 174, 239);
}

.form2__label {
  position: absolute;
  left: 1rem;
  top: 0.8rem;
  padding: 0 0.5rem;
  color: #fff;
  cursor: text;
  transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
}

.eye {
  position: absolute;
  margin-left: 280px;
  padding-top: 12px;
}

.fa {
  font-size: 20px;
}

.form2__input2:focus ~ .form2__label,
.form2__input2:not(:placeholder-shown).form2__input2:not(:focus) ~ .form2__label {
  top: -0.4rem;
  font-size: 0.9rem;
  left: 0.8rem;
}

.form2__input2:not(:-moz-placeholder-shown).form2__input2:not(:focus) ~ .form2__label {
  top: -0.4rem;
  font-size: 0.8rem;
  left: 0.8rem;
}

.form2__input2:not(:-ms-input-placeholder).form2__input2:not(:focus) ~ .form2__label {
  top: -0.4rem;
  font-size: 0.8rem;
  left: 0.8rem;
}

.login-btn {
  display: flex;
  justify-content: space-between;
  max-width: 300px;
  padding-top: 20px;
}

.login-btn button {
  display: flex;
  border: 2px solid #fff;
  border-radius: 20px;
  font-size: 15px;
  font-weight: 500;
  height: 40px;
  padding: 17px 40px;
  align-items: center;
  background: #fff;
}

/* cart */

.cart-section {
  display: flex;
  margin-bottom: 30px;
}

.root {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  grid-gap: 20px;
  margin: 15px;
  width: 100%;
}

.sidebar {
  border-radius: 5px;
  background: #eee;
  padding: 15px;
  text-align: center;
}

.head {
  background: #0062be;
  border-radius: 3px;
  height: 40px;
  padding: 10px;
  margin-bottom: 20px;
  color: #fff;
  display: flex;
  align-items: center;
}

.foot {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  padding: 10px 0;
}

/* cards */
.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  box-shadow: -4px -4px 4px 0 #fff, 15px 15px 15px 0 rgba(0, 0, 0, 0.15);
  max-width: 210px;
  padding: 15px;
  border-radius: 20px;
  height: 420px;
}

.image-box-2 {
  width: 100%;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.images {
  max-width: 90%;
  max-height: 150px;
  margin-bottom: 20px;
  object-fit: cover;
  object-position: center;
}

.bottom {
  width: 100%;
  text-align: center;
  display: flex;
  font-size: 12px;
  align-items: center;
  justify-content: space-between;
}

.cart-button {
  position: relative;
  border: none;
  border-radius: 8px;
  background: #0062be;
  padding: 7px 20px;
  font-size: 10px;
  cursor: pointer;
  color: #fff;
}

.cart-button:hover {
  background-color: #0d2f4f;
}

/* cart items */

.rowing,
.row-img {
  width: 50px;
  height: 50px;
}

.color-price {
  display: flex;
  width: 25px;
  align-items: center;
}

.color {
  padding: 8px;
  border-radius: 50%;
  height: 15px;
  margin-left: 7px;
  border: 1.8px solid #000;
}

.bottom h4 {
  color: #0aad0a;
}

@media only screen and (max-width: 850px) {
  .root {
    grid-template-columns: repeat(3, 1fr);
  }

  .nav-bar ul {
    display: none;
  }

  .item_user {
    display: none;
  }

  .menu-bar {
    display: block;
  }

  .content .img-box {
    width: auto;
  }
}

@media only screen and (max-width: 700px) {
  .content {
    flex-direction: column;
  }

  .content .text-box h2 span {
    color: #fff;
  }

  .content .text-box {
    order: 1;
  }

  .content .img-box {
    justify-content: center;
  }
}

@media only screen and (max-width: 470px) {
  .image-des {
    display: flex;
  }

  .box {
    height: auto;
    max-width: 500px;
  }
}

@media only screen and (max-width: 350px) {
  .root {
    grid-template-columns: repeat(1, 1fr);
  }

  .image-des {
    display: flex;
    flex-direction: column;
  }

  .cart-button {
    margin-top: 10px;
  }
}
